* {
  box-sizing: border-box;
}
body {
  background: #000000ce;
}
.formContainer {
  background-color: #a7bcff;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWrapper {
    background-color: white;
    padding: 20px 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;

    .logo {
      color: #5d5b8d;
      font-weight: bold;
      font-size: 24px;
    }

    .title {
      color: #5d5b8d;
      font-size: 12px;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 15px;

      input {
        padding: 15px;
        border: none;
        width: 250px;
        border-bottom: 1px solid #a7bcff;
        &::placeholder {
          color: rgb(175, 175, 175);
        }
      }

      button {
        background-color: #7b96ec;
        color: white;
        padding: 10px;
        font-weight: bold;
        border: none;
        cursor: pointer;
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        color: #8da4f1;
        font-size: 12px;
        cursor: pointer;

        img {
          width: 32px;
        }
      }
    }
    p {
      color: #5d5b8d;
      font-size: 12px;
      margin-top: 10px;
    }
  }
}
.lead_container {
  width: 700px;
  margin: 0 auto;
  padding: 10px 0;
  border-left: 2px solid #000;
  border-right: 2px solid #000;
}

.leadInput {
  background-color: #8e44ad;
  padding: 25px 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  input,
  textarea {
    width: 100%;
    padding: 15px 10px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #0000005b;
    color: #fff;
  }
  textarea {
    resize: none;
    height: 70px;
  }
  .buttons {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    button {
      padding: 10px 20px;
      border-radius: 10px;
      border: none;
      background: #16a085;
      cursor: pointer;
      font-size: 18px;
      &:first-child {
        background: rgb(134, 15, 15);
        color: #fff;
      }
    }
  }
}

.leads {
  .leadItem {
    color: #ecf0f1;
    background: #27ae60;
    margin-top: 3px;
    .firstRow {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: left;
      border: 2px solid #000;
      padding: 0 10px;
      input {
        width: 18px;
        height: 18px;
      }
      .email {
        display: flex;
        align-items: center;
        justify-content: space-between;
        img {
          width: 35px;
          height: 35px;
          object-fit: cover;
          cursor: pointer;
        }
      }
      .name {
        display: flex;
        align-items: center;
        justify-content: space-between;
        img {
          width: 35px;
          height: 35px;
          object-fit: cover;
          cursor: pointer;
        }
      }
      div {
        width: 50%;
        padding: 20px 15px;
        padding: 5px 15px;
        &:nth-child(1) {
          border-right: 1px solid #000;
        }
      }
    }
    .desc {
      padding: 20px 15px;
      border: 2px solid #000;
      border-top: 0;
    }
  }
  .buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    a,
    button {
      text-decoration: none;
      font-size: 18px;
      color: #fff;
      width: 100px;
      height: 50px;
      border-radius: 10px;
      margin: 5px auto;
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
      cursor: pointer;
      &:nth-child(1) {
        background: #1d8d71;
      }
      &:nth-child(2) {
        background: #074737;
      }
      &:nth-child(3) {
        background: #a13e10;
      }
      &:nth-child(4) {
        background: #810b0b;
      }
    }
  }
}
.cInputs {
  background: green;
  display: flex;
  flex-wrap: wrap;
  padding: 10px 20px;
  gap: 3px;
  margin-top: 10px;
  .inputItem {
    padding: 5px 10px;
    background: #00000049;
    border-radius: 5px;
    color: #ffffff9f;
    display: flex;
    align-items: center;

    button {
      background: #00000048;
      border-radius: 50%;
      border: none;
      outline: none;
      margin-left: 3px;
      color: #ffffff9f;
      font-size: 20px;
      width: 30px;
      height: 30px;
      cursor: pointer;
      text-align: center;
    }
  }
}
.home {
  background: linear-gradient(to top right, #3498db, #8e44ad, #34495e);
  height: 100vh;
  overflow-y: scroll;
  padding: 10px;
  .container {
    width: 800px;
    margin: 0 auto;
    background: rgba(0, 0, 0, 0.231372549);
    padding: 20px 30px;
    box-shadow: 2px 3px 6px #241a1a8a;
    .inputButtons {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .addButton,
      .signoutBtn {
        width: 50px;
        height: 50px;
        img {
          width: 100%;
          object-fit: cover;
          cursor: pointer;
          filter: invert(1);
          opacity: 0.7;
        }
      }
    }
    .inputs {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      margin-bottom: 30px;
      .firstRow {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        width: 100%;
      }
      input,
      textarea {
        width: 50%;
        font-size: 16px;
        padding: 10px 15px;
        border-radius: 10px;
        border: none;
        outline: none;
        background: rgba(0, 0, 0, 0.363);
        color: #fff;
      }
      textarea {
        width: 100%;
        resize: none;
        height: 100px;
        margin-top: 10px;
      }
      .buttons {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 60px;
        width: 100%;
        button {
          font-size: 16px;
          padding: 10px 20px;
          border-radius: 10px;
          margin-top: 10px;
          background: rgba(0, 0, 0, 0.596);
          border: 0;
          color: #ffffff77;
          cursor: pointer;
          &:nth-child(1) {
            background: rgba(139, 5, 5, 0.397);
            color: #fff;
          }
        }
      }
    }
    .selectedLeads {
      .leadItems {
        display: flex;
        flex-wrap: wrap;
        gap: 4px;
        color: #ffffff77;
        div {
          background: #00000081;
          padding: 10px 15px;
          border-radius: 5px;
          button {
            margin-left: 7px;
            background: rgba(0, 0, 0, 0.52);
            color: rgba(255, 255, 255, 0.5607843137);
            border-radius: 50%;
            cursor: pointer;
            border: 0;
            width: 24px;
            height: 24px;
            font-size: 20px;
            font-weight: 600;
            line-height: 12px;
            text-align: center;
          }
        }
      }
      .buttons {
        margin-top: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        button,
        a {
          font-size: 16px;
          padding: 10px 20px;
          border-radius: 5px;
          border: 0;
          outline: 0;
          color: #fff;
          cursor: pointer;
          &:nth-child(1) {
            background: #0000005e;
          }
          &:nth-child(2) {
            background: #a10a0a5e;
          }
          &:nth-child(3) {
            background: #9b0505b0;
          }
        }
        a {
          background: #125f0bc2;
          text-decoration: none;
        }
      }
    }
    .leads {
      .item {
        background: #0000006e;
        margin-top: 15px;
        padding: 10px 15px;
        color: #fff;
        img {
          width: 30px;
          height: 30px;
          flex: 2;
          object-fit: contain;
          cursor: pointer;
          &.delete {
            width: 25px;
            height: 25px;
            filter: invert(1);
          }
        }
        .firstRow {
          display: flex;
          align-items: center;
          justify-content: space-between;
          position: relative;

          .checkbox {
            width: 22px;
            height: 22px;
            flex: 1;
          }
          p {
            flex: 3;
          }
          .arrowDown {
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translateX(-50%) rotate(180deg);
            opacity: 0.6;
            top: calc(100% - 8px);
            &.hide {
              display: none;
            }
          }
          .copy {
            flex: 1;
          }
          .date {
            flex: 2;
          }
          .delete {
            flex: 1;
          }
        }

        .secondRow {
          margin-top: 15px;
          padding-top: 15px;
          border-top: 2px solid #000;
          position: relative;
          padding-bottom: 20px;
          transition: 1s;
          padding: 0 90px;
          p.desc {
            margin-top: 10px;
          }
          .secondRowCol1 {
            border-bottom: 2px solid #000;
            padding: 15px 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            p {
              flex: 1;
            }
            .copy {
              flex: 1;
              padding-right: 114px;
            }
          }
          &.hideDesc {
            display: none;
          }
          .arrowUp {
            width: 45px;
            height: 45px;
            position: absolute;
            top: calc(100% - 15px);
            left: 50%;
            transform: translateX(-50%);
            opacity: 0.7;
          }
        }
      }
    }
  }
}
